<template>

  <div class="carousel-figure-main" :style="carouselFigureStyle">
    <div class="carousel-figure-content" v-for="(item,index) in items">
      <div v-if="index===0" class="carousel-figure-selected">
        <shoes-card v-for="(shoes,index) in shoesSelectedArray" :key="shoes.id" :shoes="shoes"
                    :shoesSize="shoesCardSize" :showInfo="layoutType===0"
                    :showShare="layoutType===0" :showPick="layoutType===0"
                    @shoes-button-click="handleShoesButtonClick"
                    @shoes-click="handleShoesClick"></shoes-card>
      </div>
      <div v-if="index===1" class="carousel-figure-sell">
        <shoes-card v-for="(shoes,index) in shoesSellArray" :key="shoes.id" :shoes="shoes"
                    :shoesSize="shoesCardSize" :showInfo="layoutType===0"
                    @shoes-button-click="handleShoesButtonClick"
                    @shoes-click="handleShoesClick"></shoes-card>
      </div>
      <div v-if="index===2" class="carousel-figure-latest">
        <shoes-latest-card v-for="(shoes,index) in latestShoes" :shoes="shoes" :key="index"
                           @shoes-latest-card-click="handleShoesLatestCardClick"></shoes-latest-card>
      </div>
      <div class="height50">
        已经到底部啦...
      </div>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import shoesCard from '../../components/ShoesCard/ShoesCard.vue';
  import shoesLatestCard from '../../components/shoesLatestCard/shoesLatestCard.vue';

  export default{
    components: {
      shoesCard, shoesLatestCard
    },
    props: {
      items: {
        type: Array,
        default: []
      },
      datas: {
        type: Array,
        default: []
      }
    },
    computed: {
      carouselFigureStyle: function () {
        let _this = this;
        let screenWidth = document.body.clientWidth;
        let screenHeight = document.body.clientHeight;
        let style = {
          width: _this.items.length * screenWidth + 'px',
          left: -(_this.currentPosition * screenWidth) + 'px',
          top: _this.currentPosition === (_this.items.length - 1) ? 60 + 'px' : 110 + 'px'
        };
        return style
      },
      currentPosition: function () {
        for (let i = 0, length = this.items.length; i < length; i++) {
          if (this.items[i].active)
            return i
        }
      },
      shoesSelectedArray: function () {
        let arr = [];
        for (let i = 0, length = this.datas.length; i < length; i++) {
          if (this.datas[i].state === 0) {
            arr.push(this.datas[i]);
          }
        }
        return arr
      },
      shoesSellArray: function () {
        let arr = [];
        for (let i = 0, length = this.datas.length; i < length; i++) {
          if (this.datas[i].state === 1) {
            arr.push(this.datas[i]);
          }
        }
        return arr
      },
      shoesLatestArray: function () {
        let arr = [];
        for (let i = 0, length = this.datas.length; i < length; i++) {
          if (this.datas[i].state === 3) {
            arr.push(this.datas[i]);
          }
        }
        return arr
      },
      latestShoes: function () {
        let shoes = {};
        let hash = [];
        let shoesList = this.shoesLatestArray;
        for (let i = 0, length = shoesList.length; i < length; i++) {
          if (!hash[shoesList[i].time]) {
            if (!Array.isArray(shoes[shoes.length])) {
              shoes[shoesList[i].time] = [];
              shoes[shoesList[i].time].push(shoesList[i]);
            }
            hash[shoesList[i].time] = true;
          } else {
            shoes[shoesList[i].time].push(shoesList[i]);
          }
        }
        return shoes;
      },
      shoesCardSize: function () {
        return this.layoutType === 0 ? document.body.clientWidth : document.body.clientWidth / 2 - 4;
      }
    },
    data(){
      return {
        layoutType: 0,   //0--垂直布局  1--方格布局
      }
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
      let _this = this;
      _this.$bus.$on('change-to-right', function () {
        let data = {
          target: _this.currentPosition === (_this.items.length - 1) ? 0 : (_this.currentPosition + 1)
        };
        console.log('c t r');
        _this.$emit('carousel-figure-change', data);
      });
      _this.$bus.$on('change-to-left', function () {
        let data = {
          target: _this.currentPosition === 0 ? _this.items.length - 1 : (_this.currentPosition - 1)
        };
        _this.$emit('carousel-figure-change', data);
      });

      _this.$bus.$on('change-layout', function (data) {
        _this.layoutType = data.layout;

      })
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
      this.$off('carousel-figure-change');

    },
    methods: {
      handleShoesButtonClick(data){
        let _this = this;
        if (data.state === 0) {
//          for (let i = 0; i < _this.shoesArray.length; i++) {
//            if (_this.shoesArray[i].id === data.target) {
//              _this.shoesArray[i].price = '通知我√';
//              return;
//            }
//          }
        } else if (data.state === 1) {
          //购买
          _this.$store.commit({
            type: 'moduleShopCart/openShopCart',
            shoes: {
              id: data.shoes.id,
              name: data.shoes.name,
              theme: data.shoes.theme
            }
          })
        }
      },
      handleShoesClick(data){
        let _this = this;
        _this.$emit('open-shoes-detail', data);
      },
      handleShoesLatestCardClick(data){
        let _this = this;
        _this.$emit('open-shoes-detail', data);
      }
    }

  }
</script>
<style scoped>
  .carousel-figure-main {
    position: absolute;
    top: 110px;
    overflow: hidden;
    display: flex;
    transition: all .3s;
  }

  .carousel-figure-content {
    width: 100%;
    height: 550px;
    overflow-y: auto;
    flex-grow: 1;
  }

  .carousel-figure-selected, .carousel-figure-sell {
    display: flex;
    flex-wrap: wrap;
  }

  .height50 {
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    text-align: center;
    color: lightgray;
  }
</style>



