<template>

  <div class="menu-tools-content" :style="menuToolsStyle">
    <div v-for="(item,index) in menus" :key="index" @click="clickTools(item.id)">
      <transition name="icon">
        <i :class="'iconfont '+ item.icon" v-show="!item.active"></i>
      </transition>
      <transition name="icon">
        <i :class="'iconfont '+ item.iconActive" v-show="item.active"></i>
      </transition>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      menus: {
        type: Array,
        default: []
      },
      height: {
        type: Number,
        default: 60
      }
    },
    computed: {
      menuToolsStyle: function () {
        let _this = this;
        let style = {
          height: _this.height + 'px'
        };
        return style
      }
    }
    ,
    data()
    {
      return {
        ToolsState: 0 //0--垂直布局  1--方格布局
      }
    }
    ,
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    }
    ,
    mounted()
    {
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    }
    ,
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    }
    ,
    methods: {
      clickTools(target){
        let _this = this;
        _this.ToolsState = _this.ToolsState === 0 ? 1 : 0;
        let data = {
          target: target,
          toolsState: _this.ToolsState
        };
        this.$emit('click-menu-tools', data);
      }
    }

  }
</script>
<style scoped lang="scss">
  .menu-tools-content {
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 1px 1px 1px lightgrey;
    & div {
      flex: 1;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    & i {
      font-size: 18px;
    }
  }
  .icon-enter-active,.icon-leave-active{
    transition: all .3s;
  }
  .icon-enter,.icon-leave-to{
    opacity: 0;
    transform: translateX(-50%);
  }
  .icon-leave,.icon-enter-to{
    opacity: 1;
  }
</style>



