<template>

  <div class="Nav-Head-content">
    <div :class="item.active?'Nav-Head-item active':'Nav-Head-item'" v-for="(item,index) in items" :key="index"
         @click="handleNavClick(item.id)">
      {{item.title}}
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      items: {
        type: Array,
        default: []
      }
    },
    computed: {},
    data(){
      return {
        currentPosition: 0
      }
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      handleNavClick(target){
        let data = {
          target: target
        };
        this.$emit('nav-head-click', data);
      }
    }
  }
</script>
<style scoped>
  .Nav-Head-content {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .Nav-Head-item {
    flex: 1;
    line-height: 60px;
    font-size: 22px;
    color: darkgray;
    font-weight: bold;
  }

  .active {
    color: black;
    border-bottom: solid 3px black;
  }
</style>



