<template>

  <div class="home">
    <nav-head :items="carouselFigureArray" @nav-head-click="handleNavHeadClick"></nav-head>
    <transition name="menu-tools">
      <menu-tools :menus="menuToolsArray" :height="50"
                  v-show="!carouselFigureArray[carouselFigureArray.length-1].active"
                  @click-menu-tools="handleMenuToolsClick">
      </menu-tools>
    </transition>
    <carousel-figure :items="carouselFigureArray" :datas="shoesData"
                     @carousel-figure-change="handleCarouselFigureChange"
                     @open-shoes-detail="handleOpenShoesDetail"
    ></carousel-figure>
  </div>

</template>

<script type="text/ecmascript-6">
  import CarouselFigure from '../../components/CarouselFigure/CarouselFigure.vue';
  import NavHead from '../../components/NavHead/NavHead.vue';
  import MenuTools from '../../components/MenuTools/MenuTools.vue';


  export default{
    components: {
      CarouselFigure, NavHead, MenuTools
    },
    props: {},
    computed: {
      shoesData: function () {
        return this.$store.state.moduleShoes.shoesData;
      }
    },
    data(){
      return {
        carouselFigureArray: [
          {
            id: 1,
            title: '精选',
            active: true
          },
          {
            id: 2,
            title: '热卖中',
            active: false
          },
          {
            id: 3,
            title: '新品预览',
            active: false
          }
        ],
        carouselFigurePos: 0,
        //操作栏
        menuToolsArray: [
          {
            id: 1,
            icon: 'icon-yinhangqia-tianchong',
            iconActive: 'icon-caidan-tianchong',
            active: false,
          },
          {
            id: 2,
            icon: 'icon-iconset0174',
            iconActive: 'icon-iconset0174',
            active: false
          }
        ],
      }
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
      let _this = this;
//      _this.axios.get('/getShoes?s_id=-1').then((response) => {
//        if (response.data.code === 0) {
//          _this.shoesArray = response.data.result.shoes;
//        }
//      })
      _this.$store.dispatch({
        type: 'moduleShoes/getAllShoes',
        context: _this
      })
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      handleNavHeadClick(data){
        let _this = this;
        this.carouselFigurePos = data.target;
        for (let i = 0, length = this.carouselFigureArray.length; i < length; i++) {
          this.carouselFigureArray[i].active = data.target === this.carouselFigureArray[i].id;
        }
      },
      handleMenuToolsClick(data){
        let _this = this;
        if (data.target === 1) {
          //切换布局
          _this.menuToolsArray[0].active = data.toolsState === 1;
          let _data = {
            pos: _this.carouselFigurePos,
            layout: data.toolsState
          };
          this.$bus.$emit('change-layout', _data);
        }
      },
      handleCarouselFigureChange(data){
        for (let i = 0, length = this.carouselFigureArray.length; i < length; i++) {
          this.carouselFigureArray[i].active = i === data.target;
        }
      },
      handleOpenShoesDetail(data){
        let _this = this;
        this.axios.get('/getShoes?s_id=' + data.target).then((response) => {
          if (response.data.code === 0) {
            let detail = {
              basic: response.data.result.shoes,
              images: response.data.result.images
            };
            _this.$store.commit({
              type: 'moduleShoes/updateShoesDetail',
              shoesDetail: detail
            });
            _this.$store.commit({
              type: 'moduleShoes/shoesDetailShowOrClose'
            });
          }
        })
      },
      handleCloseShoesDetail(){
        let _this = this;
        _this.$store.commit({
          type: 'moduleShoes/shoesDetailShowOrClose'
        });
      }
    }
  }
</script>
<style scoped>
  .home {
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 60px;
  }

  .menu-tools-enter-active, .menu-tools-leave-active {
    transition: all .3s;
  }

  .menu-tools-enter, .menu-tools-leave-to {
    opacity: 0;
    transform: translateY(-50px);
  }

  .menu-tools-enter-to, .menu-tools-leave {
    opacity: 1;
    transform: translateY(0);
  }

</style>



