<template>

  <div class="shoes-latest-content">
    <div class="shoes-latest-time">{{time}}</div>
    <div class="shoes-latest-item" v-for="(item,index) in shoes" @click="shoesLatestCardClick(item)">
      <div class="shoes-latest-item-left">
        <img class="shoes-latest-item-left-img" :src="item.img">
      </div>
      <div class="shoes-latest-item-right">
        <span ref="txts">{{item.theme}}</span>
        <br>
        <span>{{timeOfWeek(item.time)}}</span>
      </div>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      shoes: {
        type: Array,
        default: []
      }
    },
    computed: {
      time: function () {
        let time = '';
        if (this.shoes.length > 0) {
          time = this.shoes[0].time
        }
        let date = new Date();
        let y = date.getFullYear();
        let m = date.getMonth() + 1;
        m = m < 10 ? '0' + m : m;
        let d = date.getDate();
        d = d < 10 ? '0' + d : d;
        date = y + '-' + m + '-' + d;
        if (date === time.substring(0, 10)) {
          time = '今日新品';
        } else if (date.substring(8, 10) === ((parseInt(time.substring(8, 10)) - 1) + '')) {
          time = '明日发布';
        } else {
          time = time.substring(0, 10);
        }
        return time;
      },
    },
    data(){
      return {}
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。
    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
      this.themeOfFormat();
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      timeOfWeek(mTime){
        let time = mTime;
        let date = new Date(time);
        date = date.toString().split(' ');
        time = time.substring(11, -1);
        switch (date[0]) {
          case 'Sun':
            time = '星期日 ' + time;
            break;
          case 'Sat':
            time = '星期六 ' + time;
            break;
          case 'Fri':
            time = '星期五 ' + time;
            break;
          case 'Thu':
            time = '星期四 ' + time;
            break;
          case 'Wed':
            time = '星期三 ' + time;
            break;
          case 'Tue':
            time = '星期二 ' + time;
            break;
          case 'Mon':
            time = '星期一 ' + time;
            break;
        }
        return time
      },
      themeOfFormat(){
        let txts = this.$refs.txts;
        for (let i = 0, length = txts.length; i < length; i++) {
          if (txts[i].offsetHeight > 27) {
            txts[i].innerText = txts[i].innerText.substring(0, 12) + '...';
          }
        }
      },
      shoesLatestCardClick(item){
        let _this = this;
        let data = {
          target: item.id
        };
        _this.$emit('shoes-latest-card-click', data);
      }
    }

  }
</script>
<style scoped lang="scss">
  $class: 'shoes-latest-';
  .#{$class}content {

  }

  .#{$class}time {
    font-size: 20px;
    font-weight: bold;
    width: 90%;
    height: 60px;
    line-height: 60px;
    background: whitesmoke;
    padding: 0 5%;
    text-align: left;
  }

  .#{$class}item {
    width: 100%;
    border: solid 1px lightgray;
    margin-top: -1px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-left {
      width: 40%;
      & img {
        width: 90%;
        margin-left: 5%;
      }
    }

    &-right {
      width: 50%;
      font-size: 18px;
      text-align: left;
      & span:first-child {
        font-weight: bold;
        font-size: 20px;
        height: 24px;
        line-height: 24px;
      }
    }
  }
</style>



